@import "corpStyles";

body {
    background: var(--l3-dashboard-background-gradient-start-color)
    linear-gradient(180deg, var(--l3-dashboard-background-gradient-start-color) 25%,
    var(--l3-dashboard-background-gradient-end-color) 100%);
}

main {
    font-family: "Roboto-Condensed",sans-serif;
    width: 100vw;
    height: 100vh;
}

hr.lite3-hr {
  width: 52.5%;
}

// ***************************************
// MENU
// ***************************************
.lite3-menu {
    display: flex;
    height: 5.5rem;
    padding-top: 10px;
    padding-left: 10px;

  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;

.lite3-profile-menu {
        position: relative;

    .profile-menu-person {
            padding: 10px;
        }

    .profile-menu-logo {
            position: absolute;
            top: 0;
            left: 0;
            width: 2.35rem;
            height: 2.35rem;
            background-image: var(--l3-dashboard-logo, url(../images/logo-rethink-hr-huge-without-border.png));
            background-repeat: no-repeat;
            background-size: contain;
            box-shadow: 1px 1px 1px 0 rgba(0,0,0,0.4);
            border-radius: 2px;
        }

    .profile-menu-person-pic {
            width: 4.25rem;
            height: 4.25rem;
            border-radius: 50%;
            border: 2px solid white;
            filter: grayscale(100%);
        }
    }

.profile-sd-list {
        display: flex;
        height: 5.5rem;
        align-items: center;
        -webkit-overflow-scrolling: touch;
        white-space: nowrap;

    .profile-sd {
            display: flex;
            width: 9.2rem;
            margin-left: 0.9rem;
            height: 2.1rem;
            line-height: 2.1rem;
            font-size: 0.75em;

        .profile-sd-icon {
                width: 22.5%;
                background-color: $button-secondary-gray;
                border-radius: 15px 0 0 15px;
            }

        .profile-sd-title {
                color: var(--main-green-color);
                font-weight: bold;
                width: 77.5%;
                background-color: white;
                text-align: center;
                border-radius: 0 15px 15px 0;
                padding-right: 8px; // better text centering feel
            }

        }
    }


}